import { useMachine } from "@xstate/react";
import { checkoutMachine } from "./machine/serviceA"; // renamed for clarity

export default function ServiceComponent() {
  // 'state' is now called a 'snapshot' in v5
  const [snapshot, send] = useMachine(checkoutMachine);

  // You can check state using snapshot.matches()
  const isFormEntry = snapshot.matches("formEntry");

  return (
    <>
      <div>Current Step: {String(snapshot.value)}</div>

      {/* Access context data */}
      <pre>{JSON.stringify(snapshot.context, null, 2)}</pre>

      <button onClick={() => send({ type: "PREVIOUS" })}>Previous</button>
      <button onClick={() => send({ type: "NEXT" })}>Next</button>
    </>
  );
}
